.choose-language {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px;
  position: relative;
  header {
    margin-bottom: 50px;
    color: var(--primary-color);
    font-size: 20px;
    .logo {
      margin-bottom: 30px;
    }
  }
  .lang-choices {
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (max-width: 610px) {
  .choose-language {
    overflow-x: hidden;
    header {
      .logo {
        max-width: 300px;
        margin-left: auto;
        margin-right: auto;
      }
    }
  }
}

@media screen and (max-width: 360px) {
  .choose-language {
    header {
      .logo {
        width: 180px;
        margin-left: auto;
        margin-right: auto;
      }
    }
  }
}